	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>🌡9℃ 🌦</strong></p>\n<p>今日は家族で東京タワーに行きました。</p>\n<p>早速エレベーターで大展望台へ行きました。\n何もかもがとても小さく見えて、遠くの景色がよかったです。</p>\n<p>降りるときはエレベーターではなく階段でした。\n下りとは言え、五百段以上もある階段を降りるのは相当堪えました。</p>\n<p>ようやくたどり着いたタワービル屋上で少し遊んでから、三階の食堂で昼食をとりつつ一休みしました。</p>\n<p>次に蝋人形館に入りました。\n多様な人形を見て「蝋人形職人はすごい」と感じました。</p>\n<p>その次には『不思議な散歩道』と書いてあったところに入りました。\n入ってすぐのところにある鏡で、自分の姿を見て驚きました。\nしかし凹凸面の鏡ということにすぐ気づきました。</p>\n<p>また、窓らしきものがたくさんあり、その窓には、水道の蛇口や、籠などが見えました。\nその先は窓から出ているように見えるので、手を入れてみたらすり抜けたのでびっくりしました。</p>\n<p>「そろそろ帰るよ」</p>\n<p>父が唐突に言いました。</p>\n<p>「特別展望台には行かないの？」</p>\n<p>僕は聞きましたが、どうやら母の具合が悪いとのことで、特別展望台は断念しました。\nもっと見たかった、と思いました。</p>\n<p>外に出たら雨が降っていました。\n車を止めていた場所が遠かったせいか、傘をさして待っていたにも関わらず上着が濡れてしまっていました。</p>","frontmatter":{"cover":null,"date":"1991-02-10T21:00:00+09:00","strDate":"1991/2/10","title":"日記"}}},"pathContext":{"path":"/ja/blog/1991/02/10-diary/","langKey":"ja"}}

